var sections={"Home":1,"Programs":2,"Schedule & Rate":3,"Team":4,"Calendar":5,"Contact":6};var programs={"Language":{"id":1,"items":{"Group":"Our interactive small group lessons provide fun yet solid language training for both children and adults to practice Mandarin in context.","One-On-One":"Our individual lessons offer the most personalized Chinese learning experience centered around your needs.","Online":"Our remote lessons allow you to practice Chinese with a native speaker wherever you are, at your convenience.","Heritage":"Our heritage program is tailored toward native and second generation speakers' particular needs. The program adopts \"Heritage Chinese\", a teaching series designed to support learners with exposure to Chinese at home."},"description":"Learn Chinese with native speakers online or in person.","imgSrc":"/images/Language.jpg"},"Culture":{"id":2,"items":{"Dance":"Our dance program teaches different Chinese folk dance styles including fan, ribbon, and drum. You will learn a variety of techniques in Chinese folk dance from professionally trained, hand-picked teachers.","Arts & Crafts":"Our arts and crafts projects combine culture with language education through hands on experiences.","Calligraphy":"Our calligraphy courses offer great opportunities to learn Chinese characters through ancient aesthetically pleasing studies that transform simple writing into stunning visual arts."},"description":"Enjoy fun and authentic cultural classes with experts from China.","imgSrc":"/images/Culture.jpg"},"Trip":{"id":3,"items":{"Summer Camp":"Every summer, we offer multicultural exchange programs for US children to experience Chinese immersion and Chinese children to study in the US.","Personalized Trips to China":"We have a long history of helping people learn Chinese language, business and culture through traveling and guided immersion in Chinese-speaking societies."},"description":"Journey into China with an itinerary tailored around your needs and interests.","imgSrc":"/images/Trip.jpg"},"After School":{"id":4,"items":{"Partner Schools":"We work with multiple partner schools, both private and CPS, in the Chicago region to help enrich students' after school educations with courses varying from rigourous language programs to creative Chinese cultural projects."},"description":"Immerse in Chinese language and culture with an after school program at one of our partner schools.","imgSrc":"/images/AfterSchool.jpg"}};var colors={"gray":"#686868","blue":"#0098D7","red":"#EB1064","yellow":"#FFF201"};var team=[{"FirstName":"Zhaoyi","LastName":"Liu","Title":"Executive Director","Description":"Zhaoyi Liu hails from Nanjing, China. Graduated from Nanjing University with a major in Chinese Literature, she enjoys Chinese fiction and history. With 15+ years of experience in teaching Chinese language and culture, she is undoubtedly one of the best Chinese teachers in the City of Chicago."},{"FirstName":"Xueting","LastName":"Ji","Title":"Teacher","Description":"Xueting graduated from Shenyang University with a bachelor degree in English. Prior to the U.S., Xueting taught at public k-6 schools in China for many years. She is passionate about teaching, and is proud of being part of the Chinafriends teaching team. She does her best to bring laugh and cheer to her students."},{"FirstName":"Lin","LastName":"Wan","Title":"Teacher and Guzheng Instructor","Description":"Lina is from Wuhan. She majors in Mathematics at DePaul University. She is a master in playing the traditional Chinese music instrument Guzheng. She brought lots fun and laugh in the classroom."},{"FirstName":"Xiaojing","LastName":"Liu","Title":"Remote Teacher","Description":"Alice is from Weifang, China. She gained her B.A. in Art Design, a Teaching Certificate in Teaching Chinese as a Second Language from Beijing Language and Culture University, and an M.Ed. in Instructional Technology from Southern Illinois Univerisity Edwardsville. She is an expert in using educational technology and enjoys every moment with her students in her classroom."},{"FirstName":"Jelly","LastName":"Sun","Title":"Dance Intructor","Description":"Jelly majored in dance and choreography at the Northeast Normal University. She is a dance instructor in Guangdong Province and was actively involved in the dance competitions and charity events organized and sponsored by China Central Television(CCTV) and other major TV Broadcasting Stations."},{"FirstName":"Chrissy","LastName":"Mo","Title":"Intructor","Description":"Chrissy was born and raised in Yantai, China. She graduated from Lanzhou University of Technology with a B.S. in engineering. She had Putonghua Test Certificate with Putonghua Level 2, Grade A. She enjoys working with children in Chinafriends. She believes that Chinese culture could be known by more and more people through education and communication."},{"FirstName":"Yan","LastName":"Pang","Title":"Intructor","Description":"With her deep love of Chinese traditional culture and arts, after 10 years working in pharmaceutical industry, she decided to quit and followed her passion to introduce outstanding Chinese traditional culture to US, including the language, Chinese tea culture and others. She is friendly, kind and patient. And always trying her best to help students to learn more and share knowledge with friends."}];var contacts={"email":"info@chinafriends.org","phone":"(773) 525-6000","WedClassLocation":"1535 N Dayton, Chicago, IL 60642"};var schedules=[{"className":"Toddlers 13 mo-3 yo","intro":"A 50-minute session for children (18 mo to 3 years) to attend with their parents or caregivers. Adults learn with their children to discriminate sounds and tones through exposure to Chinese stories, music, movement and games.","schedule":[{"Day":"Saturday","Time":"10:00 am-10:50 am"}]},{"className":"Playground 3-5 yo","intro":"Our specialized Mandarin playgroup for preschoolers is our most effective learning program for young children. Language skills are absorbed through music, story telling and play.","schedule":[{"Day":"Monday-Friday","Time":"9:00 am-12:00 pm"}]},{"className":"3-5 yo","intro":"These are 80-minute drop-off classes for children between 3 and 5 years old. Children are immersed in a relaxed and fun atmosphere with our native Mandarin teachers in small groups to learn Mandarin naturally through stories, music, activities, games and art. Student utilize common words and phrases including colors, shapes, numbers, foods, animals, people/family, etc...","schedule":[{"Day":"Monday-Friday","Time":"1:00 am-4:00 pm"},{"Day":"Saturday","Time":"8:30 am-9:50 am"},{"Day":"Sunday","Time":"9:00 am-10:20 am, 10:30 am-11:50 am"}]},{"className":"6-7 yo Introduction","intro":"These are 80 minute drop-off classes for children entering K-2nd grade. Children will learn words, phrases and sentences through practicing daily conversations. They will be taught through contextual and theme-based songs, games, activities, poems and role playing. We introduce Mandarin tones (pinyin), visual identification of characters and the understanding of simple commands and responses. This class is offered at Beginner (little or no prior experience) and Intermediate levels (at least 2 prior years of Mandarin instruction).","schedule":[{"Day":"Monday-Wednesday, Friday","Time":"4:15 pm-5:35 pm"},{"Day":"Saturday","Time":"8:30 am-9:50 am"},{"Day":"Sunday","Time":"10:30 am-11:50 am"}]},{"className":"Beginner 1","intro":"These are 80 minute drop-off classes for students entering 3rd-8th grade. At this level, your children will continue to advance their mastery of verbal and written Mandarin skills. This will prepare them to excel in real world interactions with native speakers.","schedule":[{"Day":"Monday-Wednesday, Friday","Time":"4:15 pm-5:35pm"},{"Day":"Saturday","Time":"10:00 am-11:20 am"},{"Day":"Sunday","Time":"9:00 am-10:20 am, 10:30 am-11:50 am"}]},{"className":"Beginner 2","intro":"These are 80 minute drop-off classes for children who would like to further improve their Chinese through games and activities. Students will further improve their abilities to excel in real world situations.","schedule":[{"Day":"Monday-Wednesday, Friday","Time":"4:15 pm-5:35pm"},{"Day":"Sunday","Time":"9:00 am-10:20 am, 10:30 am-11:50 am"}]},{"className":"Chinese Dance Class","intro":"","schedule":[{"Day":"Sunday","Time":"12:00 pm-1:00 pm"}]},{"className":"Intermediate 1","intro":"","schedule":[{"Day":"Sunday","Time":"10:30 am-11:50 am"}]},{"className":"Intermediate 2","intro":"","schedule":[{"Day":"Sunday","Time":"9:00 am-10:20 am"}]}];var rates={"Children":{"Length":"18 weeks","Intro":"Includes all books, class materials, and online support.","Rate":{"Once/Week":{"50 min":"$570","80 min":"$715","Playground":"$1,260"},"Twice/Week":{"50 min":"$1,028","80 min":"$1,240","Playground":"$2,142"}}},"Adults":{"Length":"18 weeks","Intro":"Textbooks purchased separately.","Rate":{"75 min":"$760","With Children enrolled at ChinaFriends":"$515"}},"Private":{"Intro":"Adult private rates vary depending on length, frequency, and location of class.  Contact ChinaFriends for details."},"Registration":{"One-time new family registration":"$30","Sibling discount for each additional child":"$30"}};var holidays={"Easter":"Mar.31 - Apr.1","Memorial Day":"May.26 - 28","Thanksgiving":"Nov.22 - 25","Christmas":"Dec.22 - Jan.2"};var quarters={"Spring":"Feb.2, Saturday - Jun.16, Sunday 2019"};

